import { dataComponent} from '../app/_component'

const blockAccordionList = dataComponent('accordion')

if (blockAccordionList) {
    let accordionItem = document.getElementsByClassName(
        'accordion__item-head'
    )

    for (let i = 0; i < accordionItem.length; i++) {
        accordionItem[i].addEventListener('click', function () {
            this.classList.toggle('active')

            let panel = this.nextElementSibling

            if (panel.style.maxHeight) {
                panel.style.maxHeight = null
                panel.classList.remove('active')
            } else {
                panel.style.maxHeight = panel.scrollHeight + 'px'
                panel.classList.add('active')
            }
        })
    }
}
