const baseYear = 2024;
const baseCount = 6;
const currentYear = new Date().getFullYear();
const yearDifference = currentYear - baseYear;
const currentCount = baseCount + yearDifference;

const experienceElement = document.getElementById('experience-year');
if (experienceElement) {
    experienceElement.textContent = currentCount;
} else {
    console.error('Элемент с ID "experience-year" не найден.');
}
