const blockPagePreview = document.querySelector('.page-preview__blocks')

if (blockPagePreview) {
    let currentCategory = document.querySelector(
        '.page-preview__block-list-item'
    )
    currentCategory.classList.add('active')

    const categories = document.querySelectorAll(
        '.page-preview__block-list-item'
    )
    categories.forEach((category, index) => {
        category.addEventListener('mouseover', () => {
            changeImage(category.dataset.image, category)
            changeLink(category.dataset.link)
        })
        category.addEventListener('mouseout', () => {
            resetCategory(category)
        })
    })

    function changeImage(imageName, category) {
        const imageFormat = category.dataset.imageFormat
        document
            .querySelector('picture')
            .querySelector(
                'source[srcset*=".avif"]'
            ).srcset = `../../img/${imageName}.avif`
        document
            .querySelector('picture')
            .querySelector(
                'source[srcset*=".webp"]'
            ).srcset = `../../img/${imageName}.webp`
        document
            .querySelector('picture')
            .querySelector('img').src = `../../img/${imageName}.${imageFormat}`

        if (currentCategory !== category) {
            currentCategory.classList.remove('active')
            category.classList.add('active')
            currentCategory = category
        }
    }

    function changeLink(linkUrl) {
        document.querySelector('.page-preview__block-link').href = linkUrl
    }

    function resetCategory(category) {
        if (category !== currentCategory) {
            category.classList.remove('active')
        }
    }
}
