const blockSidebar = document.querySelector('.sidebar')

if (blockSidebar) {
    let touchStartX = 0
    let touchEndX = 0

    // class blocks
    const body = document.querySelector('body')
    const sidebar = document.querySelector('.sidebar')
    const btnNav = document.querySelector('.header__nav')

    // add class
    const overflow = 'overflow'

    const dataSidebarActive = 'data-sidebar-active'
    const dataTrue = 'true'
    const dataFalse = 'false'

    btnNav.addEventListener('click', function () {
        sidebar.setAttribute(dataSidebarActive, dataTrue)
        body.classList.add(overflow)
    })

    sidebar.onclick = function (event) {
        if (event.target == sidebar) {
            sidebar.setAttribute(dataSidebarActive, dataFalse)
            body.classList.remove(overflow)
        }
    }
}
